/*

Base .scss aggregator for departmetn and program sites

*/
/*
  -solid colors
  -gradients
*/
:root {
  --as-white: #fff;
  --as-black: #000;
  --as-gray-200: hsl(0, 0%, 96%);
  --as-gray-300: hsl(0, 0%, 90%);
  --as-gray-400: hsl(0, 0%, 80%);
  --as-gray-500: hsl(0, 0%, 60%);
  --as-gray-600: hsl(0, 0%, 30%);
  --as-gray-700: hsl(0, 0%, 4%);
  --as-red-200: hsl(0, 74%, 90%);
  --as-red-300: hsl(0, 74%, 60%);
  --as-red-400: hsl(0, 74%, 40%);
  --as-red-500: hsl(0, 74%, 30%);
  --as-red-600: hsl(0, 72%, 13%);
  --as-teal-200: hsl(185, 39%, 90%);
  --as-teal-300: hsl(185, 39%, 76%);
  --as-teal-400: hsl(185, 39%, 45%);
  --as-teal-500: hsl(185, 39%, 35%);
  --as-teal-600: hsl(185, 39%, 22%); }

/*

  - all non color variables

*/
:root {
  /*
----
FONTS
----
*/
  --font-sans: "canada-type-gibson", sans-serif;
  --font-serif: "freight-macro-pro", serif;
  --base-font-size: 20px;
  --base-16: 0.666rem;
  --base-18: 0.75rem;
  --base-20: 0.8333rem;
  --base-24: 1rem;
  --base-28: 1.2rem;
  --base-36: 1.5rem;
  --base-48: 2rem;
  --base-56: 2rem;
  --base-64: 2rem;
  --base-84: 2rem;
  --light: 200;
  --regular: 400;
  --semi: 600;
  --heavy: 800;
  /*
----
LINE WIDTH AND HEIGHT
----
*/
  --line-height: 1.4;
  --line-width: 60ch;
  /*
----
BORDERS
----
*/
  --border-width: 1px;
  --border-color: var(--as-gray-400);
  --border-radius: 4px;
  /*
----
WIDTHS
  match variables from /breakpoints
  used mostly for max widh settings page elements
----
*/
  --xs: 320px;
  --small: 480px;
  --medium: 782px;
  --large: 1224px;
  --xl: 1432px;
  --xxl: 1640px;
  /*
----
MARGINS
  - margins set to control horizontal spacing for page components
  - collapse at smaller sizes
----
*/
  --margin-2: 2rem;
  --margin-3: 2rem;
  --margin-4: 2rem;
  --margin-6: 2rem;
  --space: 2rem; }
  @media (min-width: 782px) {
    :root {
      --base-font-size: 24px; } }
  @media (min-width: 782px) {
    :root {
      --base-56: 2.33rem; } }
  @media (min-width: 782px) {
    :root {
      --base-64: 2.66rem; } }
  @media (min-width: 1224px) {
    :root {
      --base-84: 3.5rem; } }
  @media (min-width: 782px) {
    :root {
      --margin-3: 3rem; } }
  @media (min-width: 782px) {
    :root {
      --margin-4: 4rem; } }
  @media (min-width: 782px) {
    :root {
      --margin-6: 6rem; } }

/*
----
font size mixins
----
*/
.base-24, .sidebar h4 {
  font-size: var(--base-24);
  line-height: var(--line-height); }

.base-28, h4, .basicSidebar .content > .pageComponent--ftb > p:first-of-type {
  font-size: var(--base-28);
  line-height: 1.4; }

.base-36, h3 {
  font-size: var(--base-36);
  margin-bottom: 0;
  margin-top: 1em; }

.base-48, h2 {
  font-size: var(--base-48);
  margin-bottom: 0.5em;
  line-height: 1em; }

.base-56 {
  font-size: var(--base-56);
  margin-bottom: 0.44em;
  line-height: 1.2em; }

.base-64, h1 {
  font-size: var(--base-64);
  line-height: 1em; }

.base-84 {
  font-size: var(--base-84);
  line-height: 0.9em;
  margin-bottom: 1em; }

/* -------------------------

Title: _base.scss
Descr: sets HTML and Body styles

--------------------------- */
html {
  font-size: var(--base-font-size);
  scroll-behavior: smooth; }

body {
  font-family: var(--font-sans);
  font-size: var(--base-font-size);
  color: var(--dark-text);
  font-weight: var(--light);
  line-height: var(--line-height);
  overflow-x: hidden;
  padding: 0 1rem;
  margin: 0; }
  @media (min-width: 782px) {
    body {
      padding: 0 2rem; } }

/* -------------------------

Title: headings.
Descr: 
    Sets basic styles for h1-h4

--------------------------- */
h1,
h2,
h3,
h4 {
  font-family: var(--font-sans);
  font-weight: var(--semi);
  color: var(--as-gray-700);
  clear: both; }

@media (min-width: 1432px) {
  h1 {
    font-size: var(--base-84); } }

h1.h1--title, h1.title {
  margin-top: 0;
  font-family: var(--font-serif);
  font-weight: var(--heavy); }

.eyebrow + h2 {
  margin-top: 0.35em !important; }

.sidebar h2 {
  font-size: calc(1.5 * var(--base-font-size)); }

/* -------------------------

Title: _images.scss
Descr: sets basic image styles

--------------------------- */
img {
  max-width: 100%;
  height: auto; }

/* -------------------------

Title: _list-layouts.scss
Descr: 
    Sets styles for all list layouts. Primarily is a list a flex or grid etc.

--------------------------- */
.flex {
  display: flex;
  gap: 1rem; }
  .flex.flex--2 div {
    flex: 50%; }

.grid {
  display: grid; }

/* -------------------------

Title: <main> layouts.
Descr: 
    main content is the <main> tag. It is where the main components are. It is a child of the .container.
  - specific grid layouts on page css's
  * _article
  * _basicPage
  * _landingPage
  * _major-minor-grad
  * _view-list

--------------------------- */
.main {
  max-width: 1432px;
  margin: 0 auto var(--margin-2) auto;
  display: grid;
  grid-gap: 2rem;
  grid-row-gap: 0;
  opacity: 1;
  transition: opacity 150ms ease-in-out; }
  .main.fade {
    opacity: 0.25;
    transition: opacity 250ms ease-in-out 250ms; }
  .main .sidebar h3 {
    margin-bottom: 1em; }

/*

Basic text styles

*/
/* -------------------------

Title: Visibilities 
Descr: any hiding or showin

--------------------------- */
.hidden {
  display: none; }

.offscreen {
  position: absolute;
  z-index: -1; }

.sr-only {
  display: block;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(1px);
  white-space: nowrap;
  position: absolute; }

/* -------------------------

Title: Article cards and teasers
Descr: 
   - article--card
   - article--card-small
   - article--teaser

--------------------------- */
.article--card .article__copy {
  padding: 1rem;
  display: grid;
  gap: 0px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "label label label" "title title title" "date byline byline"; }

.article--card .article__title {
  grid-area: title;
  margin: 0.75em 0 2em 0;
  font-weight: var(--regular);
  font-size: var(--base-20);
  line-height: 1.4; }

.article--card .article__label {
  grid-area: label;
  color: var(--as-black);
  font-size: var(--base-18);
  font-weight: var(--semi);
  margin-bottom: 0.5em; }

.article--card .article__date {
  grid-area: date;
  text-align: left;
  font-size: var(--base-16);
  margin-top: 2px; }

.article--card .article__byline {
  grid-area: byline;
  font-size: var(--base-16);
  text-align: right; }

.article--card a.card__title-link {
  display: block;
  padding-bottom: 1em;
  text-decoration: underline;
  text-decoration-color: var(--as-teal-300);
  position: relative; }
  .article--card a.card__title-link:before {
    position: absolute;
    content: "";
    width: 50%;
    height: 1px;
    bottom: 0;
    left: 25%;
    transition: all 0.2s;
    background-image: red; }
  .article--card a.card__title-link:hover:before {
    left: 0;
    width: 100%; }

.article--small-card {
  border: none;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background: none;
  box-shadow: none; }
  .article--small-card :before {
    display: none; }
  .article--small-card figure,
  .article--small-card img {
    flex: 0 0 30%;
    margin-right: 1rem;
    border-radius: var(--border-radius); }
  .article--small-card .article__copy {
    flex: 1 0 60%;
    padding: 0; }
  .article--small-card .article__title {
    grid-area: title;
    margin: 0;
    font-size: var(--base-20); }
  .article--small-card .article__label {
    font-size: var(--base-16); }

/* -------------------------

Title: Buttons and links that look like buttons
Descr: 
   - iconButton
    -- a button that has an svg icon in the middle

--------------------------- */
/*
----
ICON/SVG buttons
----
*/
